import PropTypes from "prop-types"
import React from "react"
import styled from 'styled-components'


const CvSectionItem = ({ item }) => {
  const { title, description, date, details } = item;

  const Section = styled.div`
  display: flex;
  flex-direction: column;
  `
  var sectionBody;

  if (!title) { //tech sections
    sectionBody = <div>{details}</div>
  } else if (!date) { //applications section
    sectionBody = <Section> <div>{title} {description}</div> <div>{details}</div> </Section>
  } else { // experience and education
    sectionBody = <Section> <div>{title} {date}</div> <div>{description}</div><div>{details}</div></Section>
  }
  console.log(title, description, date, details)
  return (
    <div>
      {sectionBody}
    </div>
  )
}

CvSectionItem.propTypes = {
  // title: PropTypes.string,
  // description: PropTypes.string,
  // date: PropTypes.string,
  // details: PropTypes.string,
}

CvSectionItem.defaultProps = {
  // title: 'title-missing', 
  // description: 'description-missing', 
  // date: 'date-missing', 
  // details: 'details-missing',
}

export default CvSectionItem
